.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}



.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.el-item-text {
  margin-left: 30px;
  color: grey;
  font-size: 10px
}

#mapViewDiv {
  width: 100%;
  height: 400px;
  margin: 0;
  padding: 5px;
  background: white;
  font-family: "Avenir Next W01", "Arial", sans-serif;
}

#cityStyle {
  background-color: white;
  text-align: center;
  padding: 10px;
  font-size: 0.9em;
}

#cityStyle label {
  padding-right: 10px;
  cursor: pointer;
}